.social-list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;

    @media (min-width: 1024px) {
        margin-left: auto;
    }
}

.social-list__icon {
    width: 25px;
    height: 25px;
}

.social-list__link {
    display: flex;
    padding: 15px 5px;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;

    @media (min-width: 1440px) {
        &:hover {
            fill: $white;
        }
    }
}
